import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import '../App.css'

import {connect} from 'react-redux'
import {openAula} from '../store/actions/index'
import { bindActionCreators } from 'redux';

import axios from 'axios'


// ITEMS ICONS
import imgAvisos from '../assets/images/for-courses.png'

  //mudança de páginas
  function list(){
    window.location.href = "/listItems"
  }
  function inicio(){
    window.location.href = "/inicio"
  }
  function itemClick(){
    window.location.href = "/item"
    console.log(this.state.id)
  }

  class Class extends React.Component{
    
    constructor(props){
      super(props)
      this.state = {
        classId: '566',
        tipo: 'aviso',
        aulas: [],
        carregar: 'Carregar Avisos',
        btnLoad: "visitanteBtn",
        idCourse: this.props.id,
        idAula: ''
      }
    }

    loadAvisos = async () => {
      await axios.get(`/class.json`)
              .catch(err => console.log(err))
              .then(res => {
                  const aulaAll = res.data
                  let aulas = []
                  for(let key in aulaAll){
                      aulas.push({
                          ...aulaAll[key],
                          id: key
                      })
                  }
                  
                  // consultas
                  aulas = aulas.filter(aula => {
                      return (
                        aula.idCourse === this.state.idCourse
                      )
                  })
                  this.setState({aulas: aulas})
                  
              })
    }

    componentDidMount() {
      const loadPage  = () => this.loadAvisos()
      loadPage()
    }
  
  render(){

    // Avisos
    const aulas = this.state.aulas
  
    const listAvisos = aulas.map((aula) => 
        {
        return <li className="class" key={aula.id}
          onClick={() => {
            this.setState({ idAula: aula.id }, () => {
              (this.props.openAula(this.state))
              (window.location.href = "/aula");
            });
          } }
        >
          <img src={aula.imageUrl}/>
          <p className='titleCard'> {aula.title} </p>

        </li>;
      }
    )
  
    return (
    <div>
        <section className="class-section">
            <ul  className="class-list">
              {listAvisos}
            </ul>
        </section>
      </div>
    );
  }
}


const mapStateToProps = store => {
  return{
    id: store.course.id,
  }
};

const mapDispatchToProps = dispatch => {
  return bindActionCreators({openAula}, dispatch);
}

export default connect(mapStateToProps, mapDispatchToProps)(Class);