import React, { Component } from 'react';
import ReactDOM from 'react-dom'
import '../App.css'

import { connect } from 'react-redux'
import { LoggedOut } from '../store/actions/index'
import { bindActionCreators } from 'redux';

import firebase from 'firebase'

import axios from 'axios'


// ITEMS MENU
import logo from '../assets/images/logo12.png'
import profile from '../assets/images/profile.png'

function goInicio() {
  window.location.href = "/inicio"
}

function goProfile() {
  window.location.href = "/dashboard"
}





class MainMenu extends React.Component {

  componentDidMount() {
    this.providerGoogle = new firebase.auth.GoogleAuthProvider();
    if (this.props.userId === '') {
      window.location.href = "/"
    }
  }


  goLogin = () => {
    firebase.auth().signOut().then(() => {
      this.props.LoggedOut(this.state)
      window.location.href = "/"
    })
  }

  render() {
    // menu-menu
    return (
      <nav className="main-menu">

        <img className="logo-menu" src={logo} onClick={goInicio} />

        {/* <div className="menu-items">
          <a className="itemsMenu" onClick={goInicio} href="#">início</a>
          <a className="itemsMenu" href="#">Cursos</a>
          <a className="itemsMenu" href="#">Lives</a>
        </div> */}

        <div img className="profile-items" >
          <img className="profile-icon" onClick={goProfile} src={profile} />
          <div className="dropMenu">
            <ul className="dropMenu-items">
              <li className="dropMenu-btn-li dropMenu-btn-li1" ><a className="dropMenu-btn" onClick={goProfile} href="#">Perfil</a></li>
              <li className="dropMenu-btn-li dropMenu-btn-li2"><a className="dropMenu-btn" onClick={
                (() => {
                  this.props.LoggedOut(this.state)
                  window.location.href = "/"
                }
                )
              } href="#">Sair</a></li>
            </ul>
          </div>
        </div>

      </nav>
    );
  }
}

const mapStateToProps = store => {
  return{
    email: store.user.email,
    userId: store.user.userId,
  }
};
const mapDispatchToProps = dispatch => {
  return bindActionCreators({ LoggedOut }, dispatch);
}


export default connect(mapStateToProps, mapDispatchToProps)(MainMenu)