// actions
import {
  CLICK_UPDATE,
  OPEN_AULA,
} from '../actions/actionsTypes'

const initialState = {
    id: '111',
    tipo: 'MODELO',
    idAula: '123',
  };
  export const clickReducer = (state = initialState, action) => {
    switch (action.type) {
      case CLICK_UPDATE:
        return {
          ...state,
          id: action.payload.id,
          tipo: action.payload.tipo,
        };

      case OPEN_AULA:
        return {
          ...state,
          idAula: action.payload.idAula,
      };
      default:
        return state;
    }
  };