class Gestao extends Component{
  constructor(props){
    super(props)
    this.state = {
      idAula: this.props.idAula,
      tipo: this.props.tipoItem,
      userType: this.props.tipo,
      title: '',
      description: '',
      data: '',
      teacher: 'Professor',
      uriVideo: ``,
    }
  }


  loadAula = async () => {
    await axios.get(`class/${this.state.idAula}.json`)
            .catch(err => console.log(err))
            .then(res => {
              this.setState({
                title: res.data.title,
                description: res.data.description,
                uriVideo: res.data.uriVideo,
              })
            })
  }

  componentDidMount() {
    const loadPage  = () => this.loadAula()
    loadPage()
  }

  
  
  render() {
    return (
      <div className="App">
        <MainMenu/>
        <div className="backgroundHero heroPg">
            <p><img className="backgroundHero heroPg" src={bg102}/></p>
        </div>

        <div className='box-video-aula'>
          <h1>{this.state.title}</h1>
          <ReactPlayer width='100%' height='480px' url={this.state.uriVideo} controls='true'/> 
          <div className='attr-video'>
            <div className='desc-video'>
              <p>{this.state.description}</p>
            </div>
            <div className='downloads-video'>
              <p><input type="button" value="EM BREVE" className="btn-downloads" /></p>
              <p><input type="button" value="EM BREVE" className="btn-downloads" /></p>
            </div>
          </div>
        </div>
    </div>
    )
  }
}

const mapStateToProps = store => {
  return{
    id: store.course.id,
    idAula: store.course.idAula,
  }
};

export default connect(mapStateToProps)(Gestao)
