import React, { Component } from 'react';
import ReactPlayer from 'react-player'

import axios from 'axios'

import {connect} from 'react-redux'

import bg101 from '../../assets/images/BANNER.png'
import '../../App.css'

import MainMenu from '../../components/mainMenu'


// Firebase
import firebase from 'firebase'
import firebaseConfig from '../firebaseConfig'




const API_KEY = 'ak_live_b8qQSH3mDivhqgwXhFNc5JnAggaA8m'
const BaseURL = 'https://api.pagar.me/1/subscriptions'
const EK_KEY = 'ek_live_CwgSVhbDMCSIIKt0hxCdyQ2EOHB4b1'
const IDPLAN = '548587' 



class Dashboard extends Component{
  constructor(props){
    super(props)
    this.state = {
      id: this.props.id,
      name: this.props.name,
      email: this.props.email,
      telefone: this.props.telefone,
      ddd: this.props.ddd,
      date: '12/02/2020',
      cardNumber: `**** **** **** XXXX`,
      cardFlag: 'Carregando...',
      planName: 'Carregando...',
    }
  }


  componentDidMount() {
    firebase.initializeApp(firebaseConfig)
    firebase.auth().onAuthStateChanged((signedUser) => {
      if(signedUser){
        this.setState({
          user: signedUser,
          id: signedUser.uid,
        });

        axios.get(`/users/${this.props.userId}.json`)
            .then(res => {

              // Pegando dados Sub PagarME
              const paymentData = {
                api_key: `${API_KEY}`
              }
              const options = {
                headers: {
                  'Content-Type': 'application/json'
                }
              }
              axios.get(`${BaseURL}/${res.data.idPay}`, {params: paymentData}, options)
                .then(res => {
                  this.setState({
                    planName: `${res.data.plan.name}`,
                    cardNumber: `**** **** **** ${res.data.card_last_digits}`,
                    cardFlag: `${res.data.card_brand}`,
                  })
                })
              this.setState({
                telefone: res.data.telefone,
                ddd: res.data.ddd,
                name: res.data.nome
              })
            })
            .catch(err => console.log(err))

        // 
      }
      else{
        this.setState({
          user: undefined
        });
      }
    })
  }


  render() {
    return (
    <div className="App">
      <MainMenu/>
      <div className="backgroundHero heroPg">
          <p>
            {/* <img className="backgroundHero heroPg" src={bg101}/> */}
          </p>
      </div>
      <div className='title-infos'>
        <h1>Conta</h1>
      </div>
      <div className='infos'>
        <div className='left-infos'>
          <h1>Nome:</h1>
          <p>{this.state.name}</p>
          <h1>Email:</h1>
          <p>{this.state.email}</p>
          <h1>Telefone:</h1>
          <p>{this.state.telefone}</p>
        </div>

        <div className='rigth-infos'>
          <p><a className='btn-infos' href="/alterardados">Alterar dados cadastrais</a></p>
          {/* <p><a className='btn-infos' href="#">Alterar Email</a></p> */}
          {/* <p><a className='btn-infos' href="#">Alterar Telefone</a></p> */}
        </div>
      </div>
          
      <div className='title-infos'>
        <h1>Pagamento</h1>
      </div>
      <div className='infos'>
        <div className='left-infos'>
          <h2>{this.state.planName}</h2>
          <h2>{this.state.cardNumber} - {this.state.cardFlag}</h2>
          <input type='button' className='btn-cancel' value='Cancelar Assinatura' onClick={
            () => {
              window.location.href = "/cancelarassinatura"
            }} />
        </div>

        <div className='rigth-infos'>
          <p><a className='btn-infos' href="/alterarpagamento">Alterar cartão de crédito</a></p>
          {/* <p><a className='btn-infos' href="#">Detalhes de cobrança</a></p> */}
          {/* <p><a className='btn-infos' href="#">Alterar data</a></p> */}
        </div>
      
      </div>
    
    </div>
    )
  }
}

const mapStateToProps = store => {
  return{
    email: store.user.email,
    name: store.user.name,
    telefone: store.user.telefone,
    userId: store.user.userId,
  }
};

export default connect(mapStateToProps)(Dashboard);
